@import '~slick-carousel/slick/slick.css';
@import '~slick-carousel/slick/slick-theme.css';

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  background-color: white;
  font-family: "Poppins",Arial, Helvetica, sans-serif;
}

#navbar {
  margin: 0;
  padding: 5px 0;
  width: 100%;
  height: 50px;
  position: sticky;
  top: 0;
  left: 0;
  background-color: white;
  box-shadow: 0 10px 10px -5px rgba(0,0,0,0.1);
}

#navbar ul {
  display: flex;
  margin: 10px 0 10px auto;
  width: 55%;
  justify-content: space-evenly;
  list-style: none;
}

#navbar ul li a {
  margin-left: 3px;
  color: black;
  opacity: 0.7;
  font-size: 1.2rem;
  font-family: "Poppins" ,Arial, Helvetica, sans-serif;
  text-decoration: none;
}

#navbar ul li a:hover {
  color: lightgreen;
  opacity: 1;
  text-decoration: underline;
  transition: all 0.2s;
}


.intro-image {
  width: 100%;
  height: 300px; 
  overflow: hidden; 
}

.intro-image img {
  width: 100%;
  height: 100%;
  object-fit: cover; 
}


#welcome-section {
  margin: 50px;
  display: flex;
  justify-content: center;
}

.message {
  display: flex;
  margin: 20px;
  width: 500px;
  justify-content: center;
  align-items: flex-start;
  font-size: 2rem;
  font-family: "Poppins" ,Arial, Helvetica, sans-serif;
  text-align: center;
}

.message img {
  max-width: 100%;
}

section {
  margin-top: 1rem;
  padding-top: 2rem;
}

.section-heading {
  font-size: 2rem;
  text-align: center;
}

.about-container {
  display: flex;
  justify-content: center;
}

div.my-information {
  display: block;
  font-size: 1.2rem;
  text-align: left;
}

#skill-section {
  background-color: black;
  color: white;
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  justify-content: center;
  margin: auto;
}

.skill-information {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1.5s ease-out, transform 1.5s ease-out;
  padding: 2rem;
  text-align: center;
}

.skill-information.visible {
  opacity: 1;
  transform: translateY(0);
}

.skill-name {
  margin: 0;
  padding: 0;
  font-size: 1.5rem;
}

.skill-information progress {
  background-color: white;
  border: 1px solid white;
}

.skill-information progress::-webkit-progress-value {
  background-color: white;
  border-radius: 0;
}

.skill-information progress::-webkit-progress-bar {
  background-color: black;
}

.image-box {
  margin: 15px;
  flex: 0 1 calc(25% - 10px); 
  display: flex; 
  flex-direction: column; 
  align-items: center; 
  padding: 10px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
}

carousel-section {
  padding: 2rem;
  text-align: center;
}

.carousel-container {
  max-width: 860px; /* Zmniejsz szerokość kontenera */
  margin: 0 auto; /* Wycentruj karuzelę */
}

.carousel-image {
  max-width: 300px;
  width: 100%;
  height: auto;
  object-fit: contain; /* Upewnij się, że obraz nie jest ucięty */
}

.carousel-description {
  text-align: left;
  font-size: 0.9rem;
  color: #555;
  margin: 0;
  list-style-type: disc; /* Wypunktowana lista */
  padding-left: 1.5rem; /* Dodaj odstęp od lewej, aby punkty były wyraźnie widoczne */
  max-width: 400px; /* Ogranicz szerokość opisu */
}

.image-box img {
  min-width: 200px;
  max-width: 400px; 
  height: 400px; 
  object-fit: cover; 
}

.image-box div {
  text-align: center; 
  padding-top: 10px; 
}

.image-box a {
  text-decoration: none;
  color: inherit; 
}

#contact-section {
  margin-top: 50px;
  padding: 50px;
  background-color: black;
  color: white;
  font-size: 1.5rem;
  text-align: center;
}

#contact-section a {
  color: white;
  margin-right: 10px;
  text-decoration: none;
}



